import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { EnquiryListComponent } from './enquiry-list.component';
import { EnquiryCommentsComponent } from './enquiry-comments/enquiry-comments.component';
import { EnquiryListResolver } from './enquiry-list.resolver';


const routes: Routes = [
  { path: '', component: EnquiryListComponent,
    children: [
      {
        path         : 'comments/:id',
        component    : EnquiryCommentsComponent,
        canActivate: [AuthGuard],
        resolve: {
          category: EnquiryListResolver
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EnquiryListRoutingModule { }
