import { Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, BehaviorSubject, Subject } from 'rxjs';

import { MatDrawerToggleResult } from '@angular/material/sidenav';

import { EnquiryListComponent } from 'src/app/modules/admin/enquiries/enquiry-list/enquiry-list.component';

import { EnquiryService } from 'src/app/api/controllers/Enquiry';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';


@Component({
  selector: 'app-enquiry-comments',
  templateUrl: './enquiry-comments.component.html',
  styleUrls: ['./enquiry-comments.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquiryCommentsComponent implements OnInit {

  isDataLoaded:boolean = false;
  isError:boolean = false;

  private _unsubscribeAll: Subject<any>;

  commentlist:any;

  message: any;

  allData: any;

  _commentsList:any;
  fullCommentsList:any = [];

  selectedItem:any;

  preferredStyleList = [];

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _enquiryListComponent: EnquiryListComponent,
    private enquiryService: EnquiryService,
    private sessionService: SessionService,
    private sharedService:SharedService
  ) {

     // Set the private defaults
     this._unsubscribeAll = new Subject();

     this.message = null;

     this._commentsList = new BehaviorSubject(null);

  }

  get comments$(): Observable<any>{
    return this._commentsList.asObservable();
  }

  closeDrawer(): Promise<MatDrawerToggleResult>{
    return this._enquiryListComponent.matDrawer.close();
  }

  goBack() {
    this._enquiryListComponent.matDrawer.close();
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  getPreferredStyle(id) {

    var data = this.preferredStyleList.filter(item => {
      return item.id === parseInt(id) ? item : '';
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  ngOnInit(): void {

    //Open the drawer
    this.sharedService.matdraweridcast.subscribe(id => {

      if(id != null && id == this._activatedRoute.params['value'].id){
        this._enquiryListComponent.matDrawer.open();
        this.selectedItem = this._enquiryListComponent.selectedItem
        this.preferredStyleList = this._enquiryListComponent.preferredCategoryList
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

    let params = {
      UserId: this.sessionService.userId,
      EnquiryId: this._activatedRoute.params['value'].id
    }

    this.enquiryService.getEnquiryComment(params).subscribe((res:any) => {

      this.isDataLoaded = true

      // Hide the message
      this.message = null;

      if(res.value) {
        this.fullCommentsList = res.value;
        this.allData = res.value;
        this._commentsList.next(res.value);
        console.log(res.value)
      }
      else {

        this.isError = true;

        // Show the error message
        this.message = {
          appearance: 'outline',
          content   : res.errors,
          shake     : true,
          showIcon  : false,
          type      : 'error'
        };

      }

    }, error => {

      this.isDataLoaded = true
      this.isError = true;

       // Show the error message
       this.message = {
         appearance: 'outline',
         content   : 'Some error occurred',
         shake     : true,
         showIcon  : false,
         type      : 'error'
       };

    })

  }

}
