import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import { ProgramService } from 'src/app/api/controllers/Program';
import { EnquiryService } from 'src/app/api/controllers/Enquiry';
import { StyleService } from 'src/app/api/controllers/Style';
import { BatchService } from 'src/app/api/controllers/Batch';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';

@Component({
  selector: 'enquiry-list',
  templateUrl: './enquiry-list.component.html',
  styleUrls: ['./enquiry-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class EnquiryListComponent implements OnInit {

  isDataLoaded:boolean = false;
  isError:boolean = false;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  _enquiresList:any;
  fullEnquiresList:any = [];

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 5;

  allData: any;

  message: any;

  programList = [];
  preferredStyleList = [];
  preferredCategoryList = [];

  selectedItem:any;

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private programService:ProgramService,
    private enquiryService: EnquiryService,
    private batchService: BatchService,
    private styleService: StyleService,
    private sessionService: SessionService,
    private sharedService:SharedService
  ) {

    this._unsubscribeAll = new Subject();

    this.message = null;

    this._enquiresList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});

    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  onGlSearchFilter(event) {

  }

  get enquires$(): Observable<any>{
    return this._enquiresList.asObservable();
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  getEnquiryFor(id) {

    var data = this.programList.filter(item => {
        return item.id === parseInt(id) ? item : '';
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  getPreferredStyle(id) {

    var data = this.preferredStyleList.filter(item => {
      return item.id === parseInt(id) ? item : '';
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  getPreferredCategory(id) {

    var data = this.preferredCategoryList.filter(item => {
      return item.id === parseInt(id) ? item : '';
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  showTrackingComments(event, item){

    event.stopPropagation();

    this.sharedService.setMatDrawerId(item.id)
    this.selectedItem = item;

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    this._router.navigate([this._router.url+'/comments', item.id], {relativeTo: route});

    //Mark for check
    this._changeDetectorRef.markForCheck();

  }

  editKid(item) {

  }

  deleteKidItem(id) {

  }

  onBoardStudent() {

  }

  ngOnInit(): void {


    let params = {
      UserId: this.sessionService.userId,
      ProgramId: 1
    }


    this.enquiryService.getEnquiryList(params).subscribe((res:any) => {

      this.isDataLoaded = true

      // Hide the message
      this.message = null;

      if(res.value) {
        this.fullEnquiresList = res.value;
        this.allData = res.value;
        this._enquiresList.next(res.value);
      }
      else {

        this.isError = true;

        // Show the error message
        this.message = {
          appearance: 'outline',
          content   : res.errors,
          shake     : true,
          showIcon  : false,
          type      : 'error'
        };

      }

    }, error => {

      this.isDataLoaded = true
      this.isError = true;

       // Show the error message
       this.message = {
         appearance: 'outline',
         content   : 'Some error occurred',
         shake     : true,
         showIcon  : false,
         type      : 'error'
       };

    });

    let prorgramListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.programService.getProgramList(prorgramListParams).subscribe((res:any) => {

      if(res.value) {
        this.programList = res.value;
      }

    })

    let categoryListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.batchService.getBatchCategoryList(categoryListParams).subscribe((res:any) => {

      if(res.value) {
        this.preferredCategoryList = res.value;
      }

    })

    let styleListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.styleService.getStyleList(styleListParams).subscribe((res:any) => {

      if(res.value) {
        this.preferredStyleList = res.value;
      }

    })

    this.enquires$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    });

  }

  ngOnDestroy(): void
    {
        // Unsubscribe from all subscriptions
        this._unsubscribeAll.next();
        this._unsubscribeAll.complete();
    }

}
